<template>
  <scroll-view class="category-tabs" scroll-x :style="{ opacity: show ? 1 : 0 }">
    <text 
      v-for="category in categories" 
      :key="category.id"
      :class="['tab', { active: category.id === activeCategory }]"
      @tap="onCategoryTap(category.id)">
      {{ category.name }}
    </text>
  </scroll-view>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: true
    },
    categories: {
      type: Array,
      default: () => []
    },
    activeCategory: {
      type: String,
      default: ''
    }
  },
  methods: {
    onCategoryTap(categoryId) {
      this.$emit('change', categoryId)
    }
  }
}
</script>

<style>
.category-tabs {
  white-space: nowrap;
  padding: 0 20rpx 20rpx;
  transition: opacity 0.3s ease;
}

.tab {
  display: inline-block;
  padding: 10rpx 30rpx;
  margin-right: 20rpx;
  border-radius: 30rpx;
  background-color: #f5f5f5;
  font-size: 28rpx;
}

.tab.active {
  background-color: #ffcc00;
  color: #fff;
}
</style>